@font-face {
  font-family: 'SerifGothicStd';
  src: url(data:font/truetype;charset=utf-8;base64,) format('openType');
  font-weight: normal;
  font-style: normal;
}

body {
  min-height: 900px;
}

.iframeAttestation {
  width: 1px; 
  height: 1px;
  opacity: 0;
  visibility: hidden;
}

.header {
  display: flex;
  justify-content: space-between;
}

.MuiFormControl-root {
  width: 100%;
}

.roomCardsContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px 60px;
}

.discountOptionContainer, .discountButtonContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.discountCopyContainer {
  font-size: 0.9rem;
  color: #3f51b5;
  text-align: center;
}

.applyDiscountCopyContainer {
  font-size: 0.6rem;
  color: #f50057;
  text-align: center;
}

